
.block-live-room-top {
  position: absolute;
  top: .5rem;
  width: 100%;
  z-index: 9;
}
.live-room {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.block-live-room-player {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 9;
}
.block-live-room-player .cover {
  width: 100%;
  height: 100%;
}
.block-live-room-interact {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: 9;
}
.block-live-room-message {
  position: absolute;
  bottom: 2.575rem;
  left: 0;
  z-index: 9;
  width: 100%;
}
.block-live-room-message.playback {
  bottom: 4.075rem;
}
.block-live-room-form {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  z-index: 9;
}
.block-live-room-giftshow {
  position: absolute;
  bottom: 48vh;
  left: 0;
  width: 100%;
  z-index: 9;
}
.block-live-room-giftshow.playback {
  bottom: 54vh;
}
.live-cover {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 10;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
}
.live-cover .btn-start-play {
  width: 2.5rem;
  height: 2.5rem;
}



